<template>
  <div>
    <section class="head">
      <div><a @click="goBack" class="iconfont back"> &#xe614; </a></div>
      <h1>Reset Password</h1>
      <div class="chartWrap"></div>
    </section>
    <div class="center">
      <span class="tisp"
        >Enter the Email address or mobile number when you registered. We will
        send you a code to reset password.</span
      >
      <van-field v-model="username" placeholder="Email or mobile number" />
      <p class="errorTips" id="error"></p>
    </div>

    <van-button type="danger" @click="applyReset">Verify</van-button>
  </div>
</template>

<script>
import { sendEmail, sendSms } from "@/api/msg";
export default {
  name: "resetPwd",
  data() {
    return {
      username: ""
    };
  },
  computed: {},
  mounted() {
    this.init();
  },
  methods: {
    init() {},
    changeSort(val) {
      this.sortValue = val;
      this.sortVisible = false;
    },
    goBack() {
      this.$router.back(-1);
    },
    applyReset() {
      var code='';
      if(this.username == ''){
        var oDiv=document.getElementById("error");
        oDiv.innerHTML='<a class="iconfont"> &#xe657; </a>Please fill in it';
        return false;
      }
      let func = this.username.indexOf("@") > -1 ? sendEmail : sendSms;
      func(this.username, "forget").then(res => {
        this.$toast("Code send successfully");
        console.log(421)
        this.$router.push({
          path: "verifyCode",
          query: {
            username: this.username,
            type: "forget",
            accountType: this.username.indexOf("@") > -1 ? 1 : 2
          }
        });
      }).catch(err => {
        var oDiv=document.getElementById("error");
        oDiv.innerHTML='<a class="iconfont"> &#xe657; </a>Wrong Email or phone NO.';
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.center {
  width: 100%;
  padding: 0 6%;
  border-top: 10px solid #f4f4f4;
  padding-top: 20px;
  .van-cell {
    padding-top: 30px;
    padding-left: 0;
    border-bottom: solid 1px #f2f3f5;
  }
  .errorTips{
    width: 100%;
    color: #e33946;
  }
}
.tisp {
  font-size: 13px;
  color: #666666;
}
.van-button--danger {
  width: 92%;
  margin-left: 4%;
  margin-top: 102px;
}
</style>



